@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
h1,h2{text-align:center;padding:0;margin:0;color:#fff}h1{font-weight:300;font-size:3em;color:#dd2c00;text-transform:uppercase}h2{font-weight:900;font-size:1.5em;color:#00bcd4}.box{background-color:#fff;border-radius:2px;padding:0 10px}.box.box-orange{background-color:#ff5722;padding:10px;color:#fff}.message{display:flex;align-items:center;justify-content:left}#form-estinzione{margin:10px 0}#box-estinzioni ul{padding:10px 0}#box-estinzioni ul li{border-bottom:1px solid #dd2c00;display:flex;flex-direction:row;justify-content:space-between;align-items:stretch;background-color:#fff;margin-bottom:1px}#box-estinzioni ul li span{padding:10px;color:#00bcd4}#box-estinzioni ul li span strong{color:#333}#box-estinzioni ul li:last-child{border-bottom:0}#box-estinzioni ul li button{font-weight:900;background-color:#00bcd4;border:0;color:#fff;display:flex;align-items:center;font-size:0.8em;cursor:pointer}#box-estinzioni #total-estinzioni{padding:10px}#box-estinzioni #total-estinzioni .total-estinzione{display:flex;flex-direction:row;justify-content:space-between;padding:5px 0;font-weight:900;font-size:1.2em;border-bottom:1px solid #dd2c00}#box-estinzioni #total-estinzioni .total-estinzione div:last-child{text-align:right}#box-estinzioni #total-estinzioni .total-estinzione:last-child{border-bottom:0}label{display:block;font-size:12px;font-weight:700;margin-bottom:5px;color:#fff}.input-container{border:1px solid #dd2c00;border-radius:2px;margin-bottom:5px;overflow:hidden;display:flex}.input-container span{width:25px;font-size:12px;background-color:#00bcd4;text-align:center;color:#fff;align-items:center;display:flex;justify-content:center}input,select{outline:none;transition:font-size 0.2s ease-in-out;height:1.8em;line-height:1.8em;background-color:#fff;font-size:1.2em}input:focus,select:focus{font-size:1.3em}input{border:0;width:calc(100% - 25px);padding:10px}select{width:100%;border:0;border-radius:0;font-family:'Lato', sans-serif;-webkit-appearance:none;-webkit-border-radius:0;padding:0 10px}input[type=submit]{width:100%;padding:5px;background-color:#00bcd4;color:#fff;border:1px solid #00bcd4;border-radius:2px;margin-top:10px;cursor:pointer;line-height:normal}::-webkit-input-placeholder{opacity:1;color:#333}:-ms-input-placeholder{opacity:1;color:#333}::-ms-input-placeholder{opacity:1;color:#333}::placeholder{opacity:1;color:#333}table{width:100%;font-size:14px;background:#fff;border-collapse:collapse;text-align:center;table-layout:fixed}th{border-width:0;padding:10px 0;font-size:15px;background-color:#ff5722;border-bottom:1px solid #e1e1e1;color:#fff}td{border-color:#e1e1e1;border-width:0 1px 0 1px;border-style:solid;border-collapse:collapse;padding:5px 0;color:#333}td:first-child{border-left:0}td:last-child{border-right:0}tr:nth-child(even){background:#e1e1e1;color:#fff}tr:nth-child(odd){background:#fff}.empty-table td{background-color:#fff;padding:20px 0}*{box-sizing:border-box}ul,li{list-style:none;margin:0;padding:0}body{background:#fff;color:#00bcd4;font-size:14px;margin:0;padding:0;font-family:'Lato', sans-serif}#app-container{display:flex;flex-direction:column;padding:10px}.row{display:flex;flex:1 1}.row:last-child{flex:11 1}.row:last-child .column{overflow:auto;padding-top:0}@media (max-width: 1024px){.row{flex-direction:column}}.column{width:15%;padding:10px;padding-left:0}.column:last-child{width:85%;display:flex;padding-right:0}@media (max-width: 1024px){.column:last-child{width:auto}}@media (max-width: 1024px){.column{width:auto;padding:10px 0}}#head-wrapper.box{background-color:#00bcd4;color:#fff;display:flex;align-content:center;padding:10px 20px;width:100%}@media (max-width: 500px){.hide-on-mobile{display:none}}

